@font-face {
    font-family: 'Chicago';
    font-style: normal;
    font-weight: normal;
    src: url("./assets/Chicago.woff") format("woff");
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
}

.ipod {
    width: calc(2.4px * 110);
    height: calc(4.1px * 110);
    padding: 28px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    background-color: hsl(0, 0%, 95%);
    box-shadow: -1px 0px 10px hsl(0, 0%, 0%) inset;

    /* non-standard fun */
    -webkit-box-reflect: below 0 linear-gradient(transparent, rgba(255, 255, 255, 0.384));
}

.screen-container {
    width: 185px;
    height: 146px;
    padding: 2px;
    border: 3px solid hsl(0deg 0% 28%);;
    border-radius: 6px;
}

.screen {
    width: 100%;
    height: 100%;
    font-family: 'Chicago', monospace;
    font-size: 14px;
    background-color: hsl(219deg 89% 92%);
    color: hsl(219deg 89% 18%);
}

.screen-header {
    margin-bottom: 1px;
    border-bottom: 2px solid hsl(219deg 89% 18%);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.screen-content {
    height: 113px;
    overflow: hidden;
}

.screen-menu {
    list-style: none;
}

.menu-item {
    padding: 0 6px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu-item .cheveron-right {
    height: 10px;
    fill: hsl(219deg 89% 18%);
}


.menu-item.is-active {
    color: hsl(219deg 89% 92%);
    background-color: hsl(219deg 89% 18%);
}

.menu-item.is-active .cheveron-right {
    fill: hsl(219deg 89% 92%);
}

.header-icon {
    width: 20px;
    height: 20px;
}

.header-icon .icon {
    fill: hsl(219deg 89% 18%);
}

.icon.battery {
    height: inherit;
    width: inherit;
}

.controls {
    margin-top: 15px;
    align-self: stretch;
    display: flex;
    justify-content: space-between;
}

.control {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Helvetica, sans-serif;
    font-size: 9px;
    font-weight: bold;
    color: tomato;
    border-radius: 50%;
    box-shadow: 1px 1px 4px hsla(0, 0%, 0%, 0.2) inset;
}

.control .icon {
    fill: tomato;
}

.control .icon.playpause {
    height: 18px;
    width: 18px;
}

.control .icon.fast-forward,
.control .icon.rewind {
    height: 22px;
    width: 22px;
}

.wheel {
    margin-top: 15px;
    width: 170px;
    height: 170px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 1px 4px hsla(0, 0%, 0%, 0.2) inset;
    border-radius: 50%;
}

.wheel-inner {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-shadow: 1px 1px 4px hsla(0, 0%, 0%, 0.2) inset;
}